@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';
@import '../../__data__/style/colors.module.css';

.main {
    background-color: var(--white);
}

.container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 var(--offset-sm);
    padding-top: 132px;
}

.mainBody {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.banner {
    align-items: center;
    min-width: 0;
}

.products {
    margin: 0 auto;
    margin-top: var(--offset-md2);
}

.productsWrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px;
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .container {
        padding-top: 104px;
    }
    .products {
        margin-top: var(--offset-md);
    }
}

@media screen and (max-width: 731.98px) {
    .container {
        padding-top: 76px;
    }
    .products {
        margin-top: var(--offset-sm);
    }
}
