@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';
@import '../../__data__/style/colors.module.css';

.product-container {
    flex: 0 1 33.333%;
    padding: 0 var(--offset-sm);
    margin-bottom: var(--offset-md2);
    display: flex;
}

.product-container .productImg {
    width: 100%;
    height: 400px;
    position: relative;
    align-self: center;
    margin-bottom: var(--offset-sm);
}

.product-container .productImg > img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: contain;
}

.product-container .buttonOffset {
    padding: 0 var(--offset-sm);
}

.product-container .productLike {
    align-self: flex-end;
}

.product-container .productLike .likeButton {
    outline-color: var(--green-4);
}

.product-container .productActive .likeButton {
    color: var(--red-4);
}

.product-container .likeButton {
    border: none;
    background: transparent;
    cursor: pointer;
}

.product-container .productLike:hover .likeButton {
    color: var(--green-4);
}

.like-container {
    flex: 0 1 25%;
    padding: 0 var(--offset-sm);
    margin-bottom: var(--offset-md2);
    display: flex;
}

.like-container .productImg {
    width: 100%;
    height: 300px;
    position: relative;
    align-self: center;
    margin-bottom: var(--offset-sm);
}

.like-container .productImg > img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: contain;
}

.like-container .buttonOffset {
    padding: 0 var(--offset-xs);
}

.like-container .productLike {
    align-self: flex-end;
}

.like-container .productLike .likeButton {
    outline-color: var(--green-4);
}

.like-container .productActive .likeButton {
    color: var(--red-4);
}

.like-container .likeButton {
    border: none;
    background: transparent;
    cursor: pointer;
    color: var(--green-4);
}

.like-container .productLike:hover .likeButton {
    color: var(--black);
}

.product {
    color: var(--black);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: var(--offset-sm);
    cursor: pointer;
    border: 1px solid var(--black);
    width: 100%;
}

.product:hover {
    border: 1px solid var(--green-4);
}

.productPrice {
    letter-spacing: 0px;
}

.productPrice > strong {
    font-weight: 600;
    font-size: 24px;
}

.productPrice > small {
    margin-left: var(--offset-xs);
    font-size: 18px;
}

.productText {
    margin: 0;
    flex: 1 1 auto;
    letter-spacing: 0px;
    margin-top: var(--offset-sm);
}

.productRating {
    display: flex;
    margin-top: var(--offset-sm);
    margin-bottom: var(--offset-sm);
    color: var(--yellow-6);
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .product-container {
        flex: 0 1 50%;
        margin-bottom: var(--offset-sm);
    }
    .product-container .productImg {
        height: 320px;
    }
    .like-container {
        flex: 0 1 33.333%;
        margin-bottom: var(--offset-sm);
    }
    .like-container .productImg {
        height: 250px;
    }
    .product-container:nth-child(odd) {
        padding: 0;
        padding-left: var(--offset-sm);
    }

    .like-container:nth-child(3n + 1) {
        padding-left: var(--offset-sm);
        padding-right: 0;
    }

    .like-container:nth-child(3n) {
        padding-left: 0;
        padding-right: var(--offset-sm);
    }
}

@media screen and (max-width: 731.98px) {
    .product-container {
        margin-bottom: var(--offset-sm);
        flex: 0 1 100%;
    }
    .product-container .productImg {
        margin-bottom: var(--offset-xs);
        height: 280px;
    }
    .like-container {
        flex: 0 1 100%;
        margin-bottom: var(--offset-sm);
    }
    .like-container .productImg {
        margin-bottom: var(--offset-xs);
        height: 200px;
    }
    .productText {
        margin-top: var(--offset-xs);
    }

    .productRating {
        margin-top: var(--offset-xs);
        margin-bottom: var(--offset-xs);
    }
}
